import React, { Component } from 'react'
import WaveSurfer from 'wavesurfer.js'

import {
  WaveformLayout,
  WaveformContainer,
  Wave,
  PlayButton
} from './Waveform.styled'

// import { library } from '@fortawesome/fontawesome-svg-core'
// import {
//   faPlay,
//   faPause,
//   faFileDownload
// } from '@fortawesome/free-solid-svg-icons'

import { FiPlay, FiPause } from 'react-icons/fi'


// import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

// library.add(faPlay, faPause, faFileDownload)

class Waveform extends Component {
  state = {
    playing: false,
    hasError: false,
    ready: false
  }

  destroy = () => this.setState({ ready: false })

  ready = (isReady) => {
    // this.setState({ ready: !this.state.ready })
    this.setState({ ready: isReady })
  }

  stopPlay = () => {
    this.setState({ playing: false })
  }

  handlePlay = () => {
    this.setState({ playing: !this.state.playing })
    this.waveform.playPause()
  }

  componentDidMount() {
    const track = document.querySelector('#track' + this.props.url.substr(-8))
    this.waveform = WaveSurfer.create({
      barWidth: 3,
      cursorWidth: 1,
      container: '#waveform' + this.props.url.substr(-8),
      backend: 'MediaElement',
      height: 80,
      progressColor: '#2D5BFF',
      responsive: true,
      waveColor: '#EFEFEF',
      cursorColor: 'transparent'
    })
    this.waveform.load(track)

    this.waveform.on('ready', () => {
      const duration = this.waveform.getDuration()
      this.ready(true)
    })

    this.waveform.on('error', function (e) {
      console.warn(e)
    })

    // Go to the next track on finish
    this.waveform.on('finish', () => {
      this.stopPlay()
      // this.setState({ ready: isReady })
    })
  }

  componentDidUpdate(prevProps) {
    // Typical usage (don't forget to compare props):
    // if (this.props.isReady !== prevProps.isReady) {
    if (this.props !== prevProps) {
      const track = document.querySelector('#track' + this.props.url.substr(-8))
      this.waveform.load(track)
    }
    // }
  }

  render() {
    if (this.state.hasError) {
      // You can render any custom fallback UI
      return <h1>Something went wrong.</h1>
    }

    // const url =
    //   this.props.match !== undefined
    //     ? this.props.match.params.name
    //     : this.props.url
    const { url, name } = this.props
    return (
      <>
        {url !== '' && (
          <WaveformLayout>
            {/*name*/}
            <WaveformContainer>
              <PlayButton onClick={this.handlePlay}>
                {!this.state.playing ? (
                  <FiPlay  />
                ) : (
                  <FiPause />
                )}
              </PlayButton>
              {!this.state.ready ? (
                <div className="loader loader-bouncing is-active" />
              ) : (
                ''
              )}
              <Wave id={'waveform' + this.props.url.substr(-8)} />
              <audio id={'track' + url.substr(-8)} src={url} />
              {/*<a style={{ textAlign: 'right' }} href={url}>
                <FontAwesomeIcon
                  icon="file-download"
                  color="black"
                  className="fa-icon"
                />
              </a>*/}
            </WaveformContainer>
          </WaveformLayout>
        )}
      </>
    )
  }
}

export default Waveform
